import React, { useState } from "react";
import { ZipArchive } from "../zip_default";
import "./LoadArchive.css";
import Toolbar from "./Toolbar";
import Button from "./Button";

export default function LoadArchive ({ setArchive }: { setArchive: (archive?: ZipArchive) => void}) {

	const [isBusy, setBusy] = useState(false);
	const [errMessage, setError] = useState("");
	const [isDragActive, setDragActive] = useState(false);
	
	const on_drop = (e: React.DragEvent) => {
		e.preventDefault();
		const files = files_from_data_transfer(e.dataTransfer);

		if (files.length > 1) {
			setError("Please drop only 1 file");
		}
		else if (files.length === 1) {
			attempt_read_archive(files[0]!);
		}
	};
	const on_drag_over = (e: React.DragEvent) => {
		e.preventDefault();
		setDragActive(true);
	};
	const on_drag_leave = (e: React.DragEvent) => setDragActive(false);

	const on_file_selected = (e: React.ChangeEvent<HTMLInputElement>) => {
		const files = e.target.files;
		if (!files) {
			return;
		}

		if (files.length > 1) {
			setError("Please drop only 1 file");
		}
		else if (files.length === 1) {
			attempt_read_archive(files[0]!);
		}
	};

	const attempt_read_archive = async (file: File) => {
		setBusy(true);
		setError("");
		setDragActive(false);
		try {
			const archive = await ZipArchive.from_blob(file);
			setBusy(false);
			setArchive(archive);
		}
		catch (e) {
			setBusy(false);
			setError("Unable to read the file, is it a ZIP file?")
		}
	};

	const create_new_archive = () => {
		const archive = new ZipArchive();
		setArchive(archive);
	};

	let text = 'Drop a ZIP file here, or pick an option above to get started.';

	if (isBusy) {
		text = "Processing file...";
	}
	else if (isDragActive) {
		text = "Drop file here...";
	}
	else if (errMessage !== "") {
		text = errMessage;
	}

	return <>
		<Toolbar>
			<Button text="Create New" type="button" onClick={create_new_archive}/>
			<Button text="Pick File" type="file" onChange={on_file_selected}/>
		</Toolbar>
		<div className="load-archive__drop-zone" onDrop={on_drop} onDragOver={on_drag_over} onDragLeave={on_drag_leave}>
			{text}
		</div>
	</>;
}

function files_from_data_transfer (data_transfer: DataTransfer) {
	const files = [];
	if (data_transfer.items) {
			for (const item of data_transfer.items) {
					if (item.kind === 'file') {
							files.push(item.getAsFile());
					}
			}
	} else {
			for (const file of data_transfer.files) {
					files.push(file);
			}
	}
	return files;
}