import React from "react"
import "./Download.css";
import Button from "./Button";

type DownloadProperties = {
	url: string,
	download: string,
	text: string,
	disabled?: boolean
}

export default function Download ({ url, download, text, disabled = false }: DownloadProperties) {
	return disabled ? 
		<Button text={text} type="button"/> :
		<a className="download" href={url} download={download}>{text}</a>
}