import Dialog from "./Dialog";
import React, { useState, ChangeEvent } from "react";
import Button from "./Button";
import TextField from "./TextField";

type CloseHandler = (filename: string) => void;

type NewFileDialogProperties = {
	open: boolean,
	onClose: CloseHandler
};

export default function NewFolderDialog ({ open, onClose }: NewFileDialogProperties) {
	const [foldername, setFoldername] = useState("");

	const onNameChange = (e: ChangeEvent<HTMLInputElement>) => {
		setFoldername(e.target.value)
	};

	const handleCancel = () => {
		onClose("")
		setFoldername("");
	}
	const handleClose = () => {
		onClose(foldername.trim());
		setFoldername("");
	};
	const invalidFilename = validateFilename(foldername) === false;

	return <Dialog open={open} handleClose={handleCancel}>
		<TextField error={invalidFilename} label="Folder Name" value={foldername} onChange={onNameChange}/>
		<div>
			<Button text="Create" disabled={invalidFilename} type="button" onClick={handleClose}/>
			<Button text="Cancel" type="button" onClick={handleCancel}/>
		</div>
	</Dialog>
}

function validateFilename (filename: string) {
	filename = filename.trim();
	const slashRegex = /\\|\//g;
	if (slashRegex.test(filename)) {
		return false;
	}
	if (filename === "." || filename === ".." || filename === "") {
		return false;
	}
	return true;
}