import { ReactNode } from "react";
import React from "react";
import "./Dialog.css";

export default function Dialog ({ open, children, handleClose }: { open: boolean, children: ReactNode, handleClose: () => void}) {
	return open ? <>
		<div className="dialog-dimmer" onClick={handleClose}></div>
		<div className="dialog">{children}</div>
	</> : null;
}