import React, { useState, MouseEvent } from "react";
import "./EntryList.css";
import MenuDialog from "./MenuDialog";
import Button from "./Button";

type Entry = {
	path: string,
	filename: string,
	isFolder: boolean,
	size: number,
	getBlob: () => Promise<Blob>
}

type EntryListProperties = {
	entries: Entry[],
	cwd: string,
	setCWD: (cwd: string) => void,
	setDownload: (download: [Blob, string]|null) => void,
	deleteFile: (path: string) => void
}

export default function EntryList ({ entries, cwd, setCWD, setDownload, deleteFile }: EntryListProperties) {
	const [menuEntry, setMenuEntry] = useState<Entry|null>(null);
	const [isBusy, setBusy] = useState(false);
	const filtered_entries: Entry[] = [];
	const cwd_path = cwd + "/";

	for (const entry of entries) {
		if (entry.path !== cwd_path && entry.path.startsWith(cwd)) {
			const relative_path = entry.path.slice(cwd.length + 1);
			const slash_index = relative_path.indexOf("/");
			if (slash_index < 0 || slash_index === relative_path.length - 1) {
				filtered_entries.push(entry);
			}
		}
	}

	const onDoubleClick = (entry: Entry) => {
		if (entry.isFolder) {
			if (cwd === "") {
				setCWD(entry.filename);
			}
			else {
				setCWD(`${cwd}/${entry.filename}`);
			}
		}
	};

	const onRightClick = (entry: Entry) => {
		setMenuEntry(entry);
	};

	const createMenu = () => {
		if (!menuEntry || menuEntry.isFolder) {
			return null;
		}

		const downloadEntry = async () => {
			const entry = menuEntry;
			setBusy(true);
			setMenuEntry(null);
			const blob = await entry.getBlob();
			setBusy(false);
			setDownload([blob, entry.filename]);
		};

		const deleteEntry = () => {
			const entry = menuEntry;
			setMenuEntry(null);
			deleteFile(entry.path);
		};

		return <MenuDialog onClose={() => setMenuEntry(null)}>{
			<>
				<Button disabled={isBusy} className="menu-button" text="Download" onClick={downloadEntry}/>
				<Button disabled={isBusy} className="menu-button" text="Delete" onClick={deleteEntry}/>
				<Button disabled={isBusy} text="Cancel" onClick={() => setMenuEntry(null)}/>
			</>
		}</MenuDialog>
	}

	return <>
		{createMenu()}
		<div className="entry-list">{
			filtered_entries.map(entry => {
				return <EntryListRow onContextMenu={onRightClick} onDoubleClick={onDoubleClick} key={entry.path} entry={entry}/>
			})
		}</div>
	</>;
}

type EntryListRowProperties = {
	entry: Entry,
	onDoubleClick: (entry: Entry) => void,
	onContextMenu: (entry: Entry) => void
}

export function EntryListRow ({ entry, onDoubleClick, onContextMenu }: EntryListRowProperties) {

	const ctxHandler = (e: MouseEvent) => {
		e.preventDefault();
		onContextMenu(entry);
	}
	return <div onContextMenu={ctxHandler} onDoubleClick={() => onDoubleClick(entry)} className="entry-list-row">
		<div className="entry-list-cell" style={ { width: "20px" } }>{entry.isFolder ? "" : ""}</div>
		<div className="entry-list-cell" style={ { width: "100px" } }>{entry.isFolder ? "" : formatBytes(entry.size)}</div>
		<div className="entry-list-flex-cell">{entry.filename}</div>
	</div>
}

function formatBytes (bytes: number): string {
	if (bytes > 1e9) {
		return Math.floor(bytes / 1e9) + " GBytes";
	}
	else if (bytes > 1e6) {
		return Math.floor(bytes / 1e6) + " MBytes";
	}
	else if (bytes > 1e3) {
		return Math.floor(bytes / 1e3) + " KBytes";
	}
	else {
		return bytes + " Bytes";
	}
}