import Dialog from "./Dialog";
import React, { ReactNode } from "react";

type CloseHandler = () => void;
type MenuDialogProperties = {
	onClose: CloseHandler,
	children: ReactNode 
};

export default function MenuDialog ({ onClose, children }: MenuDialogProperties) {
	return <Dialog open={true} handleClose={onClose}>
		{children}
	</Dialog>
}