import React, { ChangeEventHandler, MouseEventHandler } from "react";
import "./Button.css";

type ButtonProperties = {
	text: string,
	type?: "file"|"button",
	disabled?: boolean,
	focusable?: boolean
	className?: string,
	onChange?: ChangeEventHandler,
	onClick?: MouseEventHandler
};

export default function Button ({ text, type = "button", onClick, onChange, focusable = true, disabled = false, className = "" }: ButtonProperties) {
	return <label className={"button " + className} tabIndex={focusable ? 0 : -1} data-disabled={disabled}>{text}<input disabled={disabled} type={type} onClick={onClick} onChange={onChange}/></label>;
}