import Dialog from "./Dialog";
import React, { useState, ChangeEvent, useEffect } from "react";
import Button from "./Button";
import TextField from "./TextField";
import Download from "./Download";

type CloseHandler = () => void;

type DownloadDialogProperties = {
	download: [Blob, string],
	onClose: CloseHandler
};

export default function DownloadDialog ({ download, onClose }: DownloadDialogProperties) {
	const [blob, default_filename] = download;
	const [filename, setFilename] = useState(default_filename);
	const [url, setURL] = useState<string>("");
	useEffect(() => {
		const url = URL.createObjectURL(blob)
		setURL(url);
		return () => {
			setURL("");
			URL.revokeObjectURL(url);
		}
	}, [blob, setURL])

	const onNameChange = (e: ChangeEvent<HTMLInputElement>) => {
		setFilename(e.target.value)
	};

	const handleClose = () => {
		onClose();
		setFilename("");
	};

	const isDisabled = filename.trim().length === 0 && url !== null;

	return <Dialog open={true} handleClose={handleClose}>
		<TextField label="File Name" value={filename} onChange={onNameChange}/>
		<div>
			<Download text="Download" disabled={isDisabled} url={url} download={filename}/>
			<Button text="Close" type="button" onClick={handleClose}/>
		</div>
	</Dialog>
}