import Dialog from "./Dialog";
import React, { useState, ChangeEvent } from "react";
import Button from "./Button";
import TextField from "./TextField";

type CloseHandler = (filename: string, blob: Blob | null) => void;

type NewFileDialogProperties = {
	open: boolean,
	onClose: CloseHandler
};

export default function NewFileDialog ({ open, onClose }: NewFileDialogProperties) {
	const [filename, setFilename] = useState("");
	const [file, setFile] = useState<Blob|null>(null);

	const onNameChange = (e: ChangeEvent<HTMLInputElement>) => {
		setFilename(e.target.value)
	};

	const onFileChange = (e: ChangeEvent<HTMLInputElement>) => {
		const files = e.target.files;
		if (files && files[0]) {
			setFile(files[0]);
			setFilename(files[0].name);
		}
		else {
			setFile(null);
			setFilename("");
		}
	};

	const handleCancel = () => {
		onClose("", null);
		setFilename("");
		setFile(null);
	}

	const handleClose = () => {
		onClose(filename.trim(), file);
		setFilename("");
		setFile(null);
	};

	const invalidFilename = validateFilename(filename) === false;
	const isDisabled = file === null || invalidFilename;

	return <Dialog open={open} handleClose={handleCancel}>
		<div>
			<TextField error={invalidFilename} label="File Name" value={filename} onChange={onNameChange}/>
			<Button text="Choose" type="file"	onChange={onFileChange}/>
		</div>
		<div>
			<Button text="Create" disabled={isDisabled} type="button" onClick={handleClose}/>
			<Button text="Cancel" type="button" onClick={handleCancel}/>
		</div>
	</Dialog>
}

function validateFilename (filename: string) {
	filename = filename.trim();
	const slashRegex = /\\|\//g;
	if (slashRegex.test(filename)) {
		return false;
	}
	if (filename === "." || filename === ".." || filename === "") {
		return false;
	}
	return true;
}