import React, { useState } from "react";
import { ZipArchive } from "../zip_default";
import Toolbar from "./Toolbar";
import EntryList from "./EntryList";
import Button from "./Button";
import NewFileDialog from "./NewFileDialog";
import NewFolderDialog from "./NewFolderDialog";
import DownloadDialog from "./DownloadDialog";

export default function ShowArchive ({ archive, setArchive }: {archive: ZipArchive, setArchive: (archive?: ZipArchive) => void}) {
	const [changeIndex, setChangeIndex] = useState(0);
	const [cwd, setCWD] = useState("");
	const [isCreateFileOpen, setCreateFileOpen] = useState(false);
	const [isCreateFolderOpen, setCreateFolderOpen] = useState(false);
	const [download, setDownload] = useState<[Blob,string]|null>(null);

	const hasChanged = () => changeIndex > 0;
	const incrementChange = () => setChangeIndex(changeIndex + 1);

	const closeArchive = () => {
		if (hasChanged()) {
			// TODO this should be a better dialog
			const result = window.confirm(`The archive has been modified, are you sure you want to close it?`);
			if (result === false) {
				return;
			}
		}
		setArchive();
	};

	const onCreateFile = async (name: string, file: Blob|null) => {
		setCreateFileOpen(false);
		if (name === "") {
			return;
		}
		await archive.set(cwd === "" ? name : cwd + "/" + name, file);
		incrementChange();
	};

	const onCreateFolder = (name: string) => {
		setCreateFolderOpen(false);
		if (name === "") {
			return;
		}
		archive.set_folder(cwd === "" ? name : cwd + "/" + name);
		incrementChange();
	};

	const downloadArchive = () => {
		const blob = archive.to_blob();
		setDownload([blob, "archive.zip"]);
	};

	const deleteFile = (path: string) => {
		archive.delete(path);
		incrementChange();
	};

	const goUpLevel = () => {
		const i = cwd.lastIndexOf("/");
		if (i < 0) {
			setCWD("")
		}
		else {
			setCWD(cwd.slice(0, i));
		}
	};

	const isRoot = cwd === "";

	const entries = getArchiveEntries(archive);

	let download_dialog = null;
	
	if (download) {
		download_dialog = <DownloadDialog download={download} onClose={() => setDownload(null)}/>;
	}

	return <>
		<Toolbar>
			<Button text="New File" type="button" onClick={() => setCreateFileOpen(true)}/>
			<Button text="New Folder" type="button" onClick={() => setCreateFolderOpen(true)}/>
			<Button text="Download" type="button" onClick={downloadArchive}/>
			<Button text="Home" type="button" disabled={isRoot} onClick={() => setCWD("")}/>
			<Button text="Up" type="button" disabled={isRoot} onClick={goUpLevel}/>
			<Button text="Close" type="button" onClick={closeArchive}/>
		</Toolbar>
		<NewFolderDialog open={isCreateFolderOpen} onClose={onCreateFolder}/>
		<NewFileDialog open={isCreateFileOpen} onClose={onCreateFile}/>
		{download_dialog}
		<EntryList entries={entries} setDownload={setDownload} deleteFile={deleteFile} setCWD={setCWD} cwd={cwd}/>
	</>;
}

type Entry = {
	path: string,
	filename: string,
	isFolder: boolean,
	size: number,
	getBlob: () => Promise<Blob>
}

function getArchiveEntries (archive: ZipArchive) {
	const entries = []
	for (const [path, entry] of archive.files()) {
		const [filename, isFolder] = getFileName(path);
		entries.push({
			path: path,
			filename,
			isFolder,
			size: entry.size,
			getBlob: () => entry.get_blob()
		});
	}
	return sortEntries(entries);
}

function getFileName (path: string): [string, boolean] {
	let slash_index = path.lastIndexOf("/");
	const is_folder = slash_index === path.length - 1;
	
	if (is_folder) {
		path = path.slice(0, -1);
	}

	slash_index = path.lastIndexOf("/");

	if (slash_index > -1) {
		path = path.slice(slash_index + 1);
	}

	return [path, is_folder];
}

function sortEntries (entries: Entry[]): Entry[] {
	return entries.sort((a: Entry, b: Entry) => {
		if (a.isFolder && b.isFolder) {
			if (a.filename > b.filename) {
				return 1;
			}
			else if (b.filename > a.filename) {
				return -1;
			}
			else {
				return 0;
			}
		}
		else if (a.isFolder) {
			return -1;
		}
		else if (b.isFolder) {
			return 1;
		}
		else {
			if (a.filename > b.filename) {
				return 1;
			}
			else if (b.filename > a.filename) {
				return -1;
			}
			else {
				return 0;
			}
		}
	})
}