import React, { useState } from 'react';
import './App.css';
import { ZipArchive } from './zip_default';
import LoadArchive from './components/LoadArchive';
import ShowArchive from './components/ShowArchive';

type AppProps = { initial_archive?: ZipArchive };

function App ({ initial_archive }: AppProps) {
	const [archive, setArchive] = useState(initial_archive);

	return <div className="app">
		<h1>WebZIP</h1>
		{
		archive ? 
			<ShowArchive archive={archive} setArchive={setArchive}/> : 
			<LoadArchive setArchive={setArchive}/>
		}
	</div>
}

export default App;
