import { ChangeEventHandler } from "react"
import React from "react"
import "./TextField.css"

type TextFieldProperties = {
	value: string,
	label?: string,
	error?: boolean,
	onChange?: ChangeEventHandler
}

export default function TextField ({ value, label = "", onChange, error = false }: TextFieldProperties) {
	return <label className={error ? "text-field error" : "text-field"}>
		<div>{label}</div>
		<input type="text" value={value}  onChange={onChange}/>
	</label>
}